:root {
    --color-content-body: #242526;
    --color-white: #fff;
    --color-border-opacity: #3983e456;
    --color-border: #3982e4;
    --color-green: #1f7533;
    --color-black: #000;
}
.content-cotainer-list > .content-item{
    padding: 15px;
    background-color: var(--color-content-body);
    border-radius: 7px!important;
}
.content-cotainer-list > .content-item > .content-top > .text-content{
    font-size: 15px;
    text-align: justify;
    letter-spacing: 0.5px;
}
.form-update{
    border: 1px solid var(--color-border);
    padding: 5px;
    border-radius: 3px;
    background-color: var(--color-white);
}
.form-update > .form-group > textarea{
    background-color: var(--color-black)!important;
    color: var(--color-white)!important;
}